/* --- icons (extends ionicons) --- */
.ion-15x {
	font-size: 1.5em;
}
.ion-2x {
	font-size: 2em;
}
.ion-3x {
	font-size: 3em;
}
.ion-4x {
	font-size: 4em;
}
.ion-fw {
  width: 1.25em;
}
.ion-muhit-tea:before {
  content: "";
  background-image: url('/images/muhit_tea_blue.svg');
  background-repeat: no-repeat;
  background-size: cover;
  display: inline-block;
  width: 1em;
  height: 1em;
  .transform(translateY(0.15em));
}
.ion-spinning {
  padding-top: 1px;
  -webkit-animation: spinning 1s infinite linear;
  -moz-animation: spinning 1s infinite linear;
  animation: spinning 1s infinite linear;
  -webkit-transform-origin: center center;
  -moz-transform-origin: center center;
  transform-origin: center center;
}
@-webkit-keyframes spinning {
    0% { -webkit-transform: rotate(0); }
  100% { -webkit-transform: rotate(360deg); }
}
@-moz-keyframes spinning {
    0% { -moz-transform: rotate(0); }
  100% { -moz-transform: rotate(360deg); }
}
@keyframes spinning {
    0% { transform: rotate(0); }
  100% { transform: rotate(360deg); }
}




/* --- main --- */
main {
	background-color: @greyLighter;
	// main height = 100% minus nav minus footer
	min-height: calc(100vh ~"-" 130px + 80px);
	// .transition(filter 0.5s ease);
  -webkit-transition: -webkit-transform 0.3s ease-in-out, -webkit-filter 0.5s ease;
  -moz-transition: -moz-transform 0.3s ease-in-out, -moz-filter 0.5s ease;
  transition: transform 0.3s ease-in-out, filter 0.5s ease;
}
.menu-isActive main {
	.transform(translateX(90vw));
}
section.login {
	background-color: @muhitBlue;
	color: @white;
  
  hr {
    border-color: @muhitBlueDark;
  }
  a:not(.btn) {
    color: @muhitBlueDark;

    &:hover, &:focus {
      color: darken(@muhitBlueDark, 10%);
    }
  }
}



/* --- rows (extends bootstrap's grid system) --- */
.row {
  .clearfix;
  max-width: 1100px;
  margin: 0 auto;
}
.row-nopadding > [class*="col-"] {
	padding-left: 0 !important;
	padding-right: 0 !important;
}
@media screen and (max-width: @screen-xs) {
  .row .col-xs-hide {
    display: none;
  }
}
@media screen and (max-width: @screen-sm) {
  .row .col-sm-hide {
    display: none;
  }
}



/* --- map --- */
#map-canvas {
  position: relative;
  padding-bottom: 50vh;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  .border-radius(10px, 10px, 0, 0);

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}



/* --- add images --- */
.add-images {
  .badge-image {
    width: 100px;
    height: 100px;
    background-color: @muhitBlue;
    color: @white;

    &:hover, &:focus {
    background-color: darken(@muhitBlue, 10%);
    }
    a {
      color: @white;
    }
    input {
      cursor: pointer;
      opacity: 0;
    }
  }
}



/* --- footer --- */
footer {
	padding: 50px 0;
	background-color: #ddd;
  -webkit-transition: -webkit-transform 0.3s ease-in-out, -webkit-filter 0.5s ease;
  -moz-transition: -moz-transform 0.3s ease-in-out, -moz-filter 0.5s ease;
  transition: transform 0.3s ease-in-out, filter 0.5s ease;
}
.menu-isActive footer {
	.transform(translateX(90vw));
}
