@navHeight: 80px;
@compactNavHeight: 70px;

/* --- nav --- */
nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  max-width: 100vw;
  height: @navHeight;
  background-color: @muhitBlue;
  color: @white;

  &.nav-isFixed {
    .box-shadow(0 3px 6px fade(@black, 20%));
  }
  &.nav-report {
    background-color: @white;

    .userinfo a {
      color: @muhitBlue;
    }
  }
  a {
    color: fade(@white, 70%);
    
    &:hover, &:focus {
      color: @white;
    }
  }
  #nav_logo {
    padding: 20px 10px;
    
    img {
      display: block;
    }
  }
  #userinfo {
    padding: 20px 15px;
    line-height: 40px;
    font-weight: 700;
  }
  .userinfo {
    #userinfo:hover,
    #userinfo:focus {
      background-color: rgba(0, 0, 0, 0.05);
    }
  }
  a#logout {
    color: @red;
    
    &:hover, &:focus {
      color: darken(@red, 10%);
    }
  }

  #nav_quickadd {
    display: none;
  }
}



/* --- nav placeholder --- */
.navplaceholder {
  display: block;
  height: @navHeight;
  background-color: @muhitBlue;
}



/* --- nav role-based colors --- */
.role-admin nav,
.role-admin .navplaceholder {
  background-color: lighten(@muhitBlueDark, 10%);
}
.role-superadmin nav,
.role-superadmin .navplaceholder {
  background-color: darken(@greyLight, 10%);
}


/* --- menu --- */
#menu {
  li {
    display: inline-block;
    margin-right: -0.25em;
  }
  a {
    display: block;
    height: @navHeight;
    padding: 18px 20px 8px;
    border-bottom: 2px solid transparent;
    font-size: 0.8em;
    font-weight: 600;
    letter-spacing: 0.05em;
    -webkit-transition: -webkit-transform 0.2s ease;
    -moz-transition: -moz-transform 0.2s ease;
    transition: transform 0.2s ease;

    &:hover, &:focus {
      background-color: fade(@black, 5%);
    }
    &:active {
      .transform(translateY(2px));
    }
    .ion {
      display: block;
    }
  }
  li.active a {
    color: @white;
    // border-bottom-color: @white;
  }
}

#navbutton {
  display: none;
  height: @navHeight;
  padding: 20px 10px;
  line-height: 30px;

  .ion {
    width: 35px;
    margin-top: 3px;
  }
  .ion-android-close {
    display: none;
  }
  &:hover, &:focus {
    background-color: fade(@black, 5%);
  }
}



@media screen and (max-width: @screen-md) {
  #menu {
    a {
      max-width: 100px;
      padding-left: 10px;
      padding-right: 10px;
    }
  }
  #userinfo {
    max-width: 180px;
  }
}



@media screen and (max-width: @screen-sm) {
  nav {
    max-width: 100vw;
    height: @compactNavHeight;

    .userinfo #userinfo {
      padding: 15px;

      .badge {
        margin-left: 0 !important;
      }
      .text {
        display: none;
      }
    }
    #nav_logo {
      padding: 15px 10px;
    }
    #nav_quickadd {
      display: inline-block;
    }
  }
  .navplaceholder {
    height: @compactNavHeight;
  }
  #menu {
    position: fixed;
    top: @compactNavHeight;
    left: 0;
    // display: none;
    width: 90vw;
    height: calc( 100vh ~"-" @compactNavHeight);
    background-color: @muhitBlueDark;
    .box-shadow(inset 0 3px 0 fade(@black, 20%));
    .transform(translateX(-90vw));
    -webkit-transition: -webkit-transform 0.3s ease-in-out;
    -moz-transition: -moz-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;

    li {
      display: block;
      margin-right: 0;

      &.active a {
        border-bottom: none;
        border-left-color: @white;
      }
    }
    li + li {
      border-top: 1px solid fade(@black, 20%);
    }
    a {
      height: 60px;
      max-width: 100%;
      padding: 13px 15px 8px;
      text-align: left;
      border-left: 5px solid transparent;

      .ion {
        display: inline-block;
        float: left;
        width: 30px;
        margin-right: 25px;
        text-align: center;
      }
    }
  }
  .menu-isActive #menu {
    // display: block;
    .transform(translateX(0));
  }
  #navbutton {
    display: inline-block;
    padding: 15px 10px;

    &.isActive {
      .ion-navicon {
        display: none;
      }
      .ion-android-close {
        display: inline-block;
      }
    }
  }
}